import * as React from 'react';
import { Link, graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import * as style from '../styles/style.module.css';

const TopPageContent1 = () => {
  const data = useStaticQuery(query); // useStaticQuery()でデータ取得
  const posts = data.allMdx.nodes; // 必要なデータを展開
  return (
    <section className={style.topContent}>
      <h2 className={style.topContent__title} data-subtitle="A collection of websites created as a trial">
        制作したもの
      </h2>
      <ul className={style.contentList}>
        {posts.map(post => {
          const image = getImage(post.frontmatter.hero_image);
          return (
            <li className={style.contentList__item} key={post.fields.slug}>
              <Link className={style.contentList__link} to={`/memo${post.fields.slug}`}>
                <figure className={style.contentThumbnail}>
                  <GatsbyImage
                    className={style.contentThumbnail__img}
                    image={image}
                    alt={post.frontmatter.hero_image_alt}
                  />
                  <figcaption className={style.contentThumbnail__title}>
                      {post.frontmatter.title}
                  </figcaption>
                </figure>
              </Link>
            </li>
          )
        })}
      </ul>
    </section>
  )
};

const query = graphql`
  query {
    allMdx(
      filter: { frontmatter: { tags: { in: ["制作したもの"] } } }
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 6
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          hero_image {
            childImageSharp {
              gatsbyImageData
            }
          }
          hero_image_alt
        }
      }
    }
  }
`;

export default TopPageContent1;
